import { HttpRequestMethod } from "@/enums/HttpRequestMethod";

const request = async (
  url: string,
  authToken: string | null,
  type: HttpRequestMethod,
  body?: unknown | null,
  form?: boolean | null
) => {
  const controller = new AbortController();
  const headers = new Headers();
  if (!form) headers.append("Content-Type", "application/json");
  if (authToken) headers.append("Authorization", `Token ${authToken}`);
  const requestOptions: { [k: string]: any } = {
    method: type,
    headers,
    cache: "no-store",
  };
  const { timeout = 8000 } = requestOptions;
  const id = setTimeout(() => controller.abort(), timeout);
  if (body) requestOptions["body"] = body;
  const response = await fetch(url, {
    ...requestOptions,
    signal: controller.signal,
  });
  if (!response.ok) throw new Error("There was an error with this request");
  const result = !form ? await response.json() : response;
  clearTimeout(id);
  return result;
};

export default {
  request,
};
