import { defineComponent } from "vue";
import ExternalLayout from "@/layouts/External/index.vue";
import routes from "@/routes";
import HttpClient from "@/util/HttpClient";
import { HttpRequestMethod } from "@/enums/HttpRequestMethod";

export default defineComponent({
  name: "Slack",
  components: {
    ExternalLayout,
  },
  data() {
    return {
      context: process.env.VUE_APP_CONTEXT,
      pars: "",
    };
  },
  async mounted() {
    try {
      const code = this.$route.query.code;
      if (code) {
        const url = `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/app/slack/oauth/`;
        const formdata = new FormData();
        formdata.append("code", code.toString());
        const response = await HttpClient.request(
          url,
          null,
          HttpRequestMethod.POST,
          formdata,
          true
        );
        console.log("response", response);
        if (!response.ok) {
          throw new Error(`${response.message} (${response["slack-message"]})`);
        }
        routes.push({
          name: "home.integrations",
          params: {
            actionOutcome: "success",
            actionMessage: "Success! You can now use Slack in your Botos :-)",
          },
        });
      } else {
        throw new Error("Could not authorize Slack");
      }
    } catch (e: any) {
      routes.push({
        name: "home.integrations",
        params: { actionOutcome: "error", actionMessage: e.toString() },
      });
    }
  },
});
